.reviews {
	position: relative;

	margin-top: 120px;

	@media(min-width: 1024px) {
		&::after {
			content: url("../img/icons/reviews-after.svg");
			position: absolute;
			top: -45px;
			right: 0;
		}
	}

	&::after {
		@media(max-width: 1024px) {
			content: url("../img/icons/reviews-after-mobile.svg");
			position: absolute;
			top: 210px;
			left: 5%;
		}
	}

	.container {


		@media(min-width: 1024px) {
			position: relative;

			display: flex;

			&::before {
				content: url("../img/icons/reviews-before.svg");
				position: absolute;
				top: -90px;
				left: -90px;

				width: 221px;
				height: 219px;
			}
		}
		&::before {
			content: url("../img/icons/reviews-before-mobile.svg");
			position: absolute;
			top: -50px;
			left: 0;
		}
	}

	&__text {
		@media(min-width: 1024px) {
			max-width: 285px;
			margin-right: 30px;
			padding-top: 108px;
		}
	}
	&__title {
		margin: 0;
		margin-bottom: 18px;

		font-size: 24px;
		text-align: center;

		@media(min-width: 1024px) {
			position: absolute;
			top: 0;

			max-width: 285px;
			margin-bottom: 32px;

			font-size: 32px;
			text-align: left;
		}
	}
	&__description {
		margin-top: 0;
		margin-bottom: 18px;

		font-size: 18px;
		text-align: center;

		@media(min-width: 1024px) {
			text-align: left;
		}
	}
	&__action {
		margin-bottom: 24px;

		font-size: 16px;
		text-align: center;
		color: #828282;

		@media(min-width: 1024px) {
			margin-bottom: 0;

			text-align: left;
		}
	}
	&__link {
		color: $accent;
		text-decoration: none;
	}
	&__slider {
		padding-top: 50px;
	}
	&__header {
		display: flex;
		justify-content: space-between;
		align-items: center;
	}

	&__slide {
		width: 280px;
		margin-left: calc(50% - 156px);
		padding: 14px 32px;

		background-image: url("../img/icons/review-bg-mobile.svg");
		background-repeat: no-repeat;
		background-size: contain;
		background-size: 328px 100%;

		@media(min-width: 450px) {
			width: 321px;
			margin-left: calc(50% - 192.5px);

			background-image: url("../img/icons/review-bg.svg");
			background-size: 381px 100%;
		}

		@media(min-width: 1024px) {
			margin-left: 0;
		}
	}








.review {
	max-width: 321px;
	padding: 14px 32px;

	&__author {
		position: relative;

		padding-left: 60px;

		&::before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;

			display: flex;
			justify-content: center;
			align-items: center;
			width: 44px;
			height: 44px;

			font-weight: 500;
			font-size: 20px;
			font-family: "Exo 2";
			text-align: center;
			color: $accent;

			background: #ecfaea;
			border-radius: 100%;
		}
	}
	&__name {
		margin: 0;

		font-weight: 300;
		font-size: 16px;
		color: $accent;
	}
	&__status {
		margin: 0;

		font-weight: 300;
		font-size: 13px;
		color: #bdbdbd;
	}


	&__text {
		margin-top: 16px;
		margin-bottom: 16px;

		font-size: 16px;
	}
	&__company {
		margin: 0;

		font-size: 14px;
		color: $accent;
	}
}
.swiper-button-prev {
	position: absolute;
	top: 10px;
	right: 0;
	left: calc(100% - 79px);
}
.swiper-button-next {
	position: absolute;
	top: 10px;
	left: calc(100% - 28px);
}

}
